@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    font-family: 'poppins', sans-serif;
}
#main{
    background-color: #FDFDf1;
    padding: 1px;
}
.wrapper{
    width: 65rem;
    height: 100rem;
    background-color: #FFF;
    margin: 60px auto;
    box-shadow: 0 15px 30px rgba(0,0,0,.2),
    0 5px 5px rgba(0,0,0,.15);
    padding-top: 4rem;
    
}

/* NAVIGATION BAR STYLING */
.navbar{
    height: 170px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.nav-split{
    width: 60%;
}
.icon{
    height: 20px;
    width: 20px;
}
.profilepic{
    height: 10rem;
}
.profile-name{
    font-size: 1.6rem;
    margin-bottom: 1.1rem;
    cursor: default;
}
.profile-detail{
    display: flex;
    align-items: center;
    margin: .5rem;
}
.profile-detail-info{
    transition: 150ms ease-in-out;
}
.profile-detail-info:hover{
    padding: 5px;
    color: #fff;
    background-color: #FF7F11;
    border-radius: 15px;
    
}
.profile-details a{
    text-decoration: none;
    color: black;
    letter-spacing: .02rem;
}
.icon{
    margin-right: .9rem;
}
/* END OF NAVIGATION BAR STYLING */

.section-heading{
    margin-top: 1.5rem;
    margin-left: 4.5rem;
    letter-spacing: .5rem;
}
.summary-detail{
    margin: .6rem 5rem 0 4.5rem;
}
.summary{
    font-size: .9rem;
}

.ed-1strow{
    display: flex;
    justify-content: space-between;
}
h5{
    font-size: .9rem;
}
.first-ed{
    margin-top: .6rem;
}
.educational-detail{
    font-size: .9rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1.5fr 1fr;
    margin-left: 4.5rem;
}
.educational-detail p:last-child{
    margin-left: 5.2rem;
}
.educational-detail p:nth-child(2){
    margin-left: 4rem;
}
.educational-detail p:nth-child(3){
    margin-left: 3rem;
}

.intern{
    font-size: .9rem;
    margin-left: 5.7rem;
}
a{
    text-decoration: none;
    color: black;
}
.intern-info{
    transition: 150ms ease-in-out;
}
.intern-info:hover{
    padding: 5px;
    background-color: #FFCFA6;
    border-radius: 15px;
}

.skill-font{
    font-size: .9rem;

}
 .skill-bar{
    width: 60%;
    height: .6rem;
    background-color: #FFCFA6;
    position: relative;
    border-radius: 10px;
}
.skill-bar.python span{
    width: 50%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.html span{
    width: 85%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.css span{
    width: 78%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.js span{
    width: 35%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.jq span{
    width: 68%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.sass span{
    width: 82%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.react span{
    width: 30%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.figma span{
    width: 65%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-bar.office span{
    width: 70%;
    height: 100%;
    background-color: #FF7F11;
    position: absolute;
    border-radius: 10px;
}
.skill-set{
    display: flex;
    align-items: center;
    justify-content: space-between;
}







@media (max-width:992px){
    .wrapper{
        height: 110rem;
    }
    .profile-name{
        font-size: 1.4rem;
        margin-bottom: .8rem;
    }
    .section-heading{
        margin-top: 1.5rem;
        margin-left: 3.5rem;
        letter-spacing: .4rem;
    }
    .summary-detail{
        margin: .6rem 4rem 0 3.5rem;
    }
    .educational-detail{
        margin-left: 3.5rem;
    }
    .educational-detail p:last-child{
        margin-left: 2.2rem;
    }
    .educational-detail p:nth-child(2){
        margin-left: 3rem;
    }
    
    .intern{
        margin-left: 4.5rem;
        margin-right: 2.9rem;
    }
}
@media (max-width: 768px){
    .wrapper{
        width: 85%;
        height: auto;
    }
    .profilepic{
        height: 9rem;
    }
    .navbar{
        height: 270px;
        align-items: flex-start;
    }
    .profile-name{
        font-size: 1.2rem;
        margin-bottom: 4rem;
    }
    .profile-details a{
        letter-spacing: 0;
    }
    .section-heading{
        margin-top: 1.2rem;
        margin-left: 1.6rem;
    }
    .summary-detail{
        margin: .6rem 2rem 0 1.6rem;
        font-size: small;
    }
    .educational-detail{
        margin-left: 1.6rem;
        font-size: 10px;
    }
    .educational-detail p:last-child{
        margin-left: 1rem;
    }
    .educational-detail p:nth-child(2){
        margin-left: 1rem;
    }
    
    .intern{
        margin-left: 2.6rem;
        margin-right: 2.9rem;
    }
}
@media (max-width: 576px){
    .wrapper{
        width:100%;
    }
    .navbar{
        height: 250px;
        align-items: flex-start;
    }
    .profile-name{
        font-size: 1.2rem;
        margin-bottom: 3.6rem;
    }
}